const commonAttrs = ['class', 'style']
const whiteListArray = [
  { name: 'a', attrs: [] },
  { name: 'abbr', attrs: [] },
  { name: 'address', attrs: [] },
  { name: 'article', attrs: [] },
  { name: 'aside', attrs: [] },
  { name: 'b', attrs: [] },
  { name: 'bdi', attrs: [] },
  { name: 'bdo', attrs: ['dir'] },
  { name: 'big', attrs: [] },
  { name: 'blockquote', attrs: [] },
  { name: 'br', attrs: [] },
  { name: 'caption', attrs: [] },
  { name: 'center', attrs: [] },
  { name: 'cite', attrs: [] },
  { name: 'code', attrs: [] },
  { name: 'col', attrs: ['span', 'width'] },
  { name: 'colgroup', attrs: ['span', 'width'] },
  { name: 'dd', attrs: [] },
  { name: 'del', attrs: [] },
  { name: 'div', attrs: [] },
  { name: 'dl', attrs: [] },
  { name: 'dt', attrs: [] },
  { name: 'em', attrs: [] },
  { name: 'fieldset', attrs: [] },
  { name: 'font', attrs: [] },
  { name: 'footer', attrs: [] },
  { name: 'h1', attrs: [] },
  { name: 'h2', attrs: [] },
  { name: 'h3', attrs: [] },
  { name: 'h4', attrs: [] },
  { name: 'h5', attrs: [] },
  { name: 'h6', attrs: [] },
  { name: 'header', attrs: [] },
  { name: 'hr', attrs: [] },
  { name: 'i', attrs: [] },
  { name: 'img', attrs: ['alt', 'src', 'height', 'width'] },
  { name: 'ins', attrs: [] },
  { name: 'label', attrs: [] },
  { name: 'legend', attrs: [] },
  { name: 'li', attrs: [] },
  { name: 'mark', attrs: [] },
  { name: 'nav', attrs: [] },
  { name: 'ol', attrs: ['start', 'type'] },
  { name: 'p', attrs: [] },
  { name: 'pre', attrs: [] },
  { name: 'q', attrs: [] },
  { name: 'rt', attrs: [] },
  { name: 'ruby', attrs: [] },
  { name: 's', attrs: [] },
  { name: 'section', attrs: [] },
  { name: 'small', attrs: [] },
  { name: 'span', attrs: [] },
  { name: 'strong', attrs: [] },
  { name: 'sub', attrs: [] },
  { name: 'sup', attrs: [] },
  { name: 'table', attrs: ['width'] },
  { name: 'tbody', attrs: [] },
  { name: 'td', attrs: ['colspan', 'height', 'rowspan', 'width'] },
  { name: 'tfoot', attrs: [] },
  { name: 'th', attrs: ['colspan', 'height', 'rowspan', 'width'] },
  { name: 'thead', attrs: [] },
  { name: 'tr', attrs: ['colspan', 'height', 'rowspan', 'width'] },
  { name: 'tt', attrs: [] },
  { name: 'u', attrs: [] },
  { name: 'ul', attrs: [] }
]
const whiteList = {}
whiteListArray.forEach(item => {
  whiteList[item.name] = item.attrs.concat(commonAttrs)
})
const RichText = {
  props: {
    nodes: {
      type: [String, Array],
      default: []
    },
    space: {
      validator: function (value) {
        return ['ensp', 'emsp', 'nbsp'].indexOf(value) !== -1
      }
    }
  },
  render(h) {
    let generateNodeString = (node, str) => {
      if (node.type === 'text') {
        let substitute = this.space ? `&${this.space};` : ' '
        str += node.text.replace(/\n/g, substitute).replace(/ /g, substitute)
      } else {
        if (node.name && whiteList[node.name.toLowerCase()]) {
          let a = node.attrs ? ` ${Object.keys(node.attrs).filter(key => whiteList[node.name.toLowerCase()].indexOf(key) !== -1).map(key => `${key}="${node.attrs[key]}"`).join(' ')}` : ''
          str += `<${node.name.toLowerCase()}` + a + '>'
          if (node.children && node.children.length) {
            node.children.forEach(child => {
              str += generateNodeString(child, '')
            })
          }
          str += `</${node.name.toLowerCase()}>`
        }
      }
      return str
    }
    let generateNodesString = (arr, str) => {
      return arr.map(item => generateNodeString(item, '')).reduce((total, current) => total + current, '')
    }
    const str = generateNodesString(this.nodes, '')
    return h('div', {
      domProps: {
        innerHTML: str
      }
    })
  },
  methods: {
  }
}
Vue.component('x-rich-text', RichText)
