const Progress = {
  props: {
    percent: {
      type: Number
    },
    showInfo: {
      type: Boolean,
      default: false
    },
    borderRadius: {
      type: Number,
      default: 0
    },
    fontSize: {
      type: Number,
      default: 16
    },
    backgroundColor: {
      type: String,
      default: 'rgb(235,235,235)'
    },
    strokeWidth: {
      type: Number,
      default: 6
    },
    activeColor: {
      type: String,
      default: 'rgb(9,187,7)'
    },
    active: {
      type: Boolean,
      default: false
    },
    activeMode: {
      type: String,
      default: 'backwards'
    },
    duration: {
      type: Number,
      default: 30
    }
  },
  mounted() {
    if (this.active) {
      this.interval = setInterval(() => {
        this.animationPercent++
      }, this.duration)
    }
  },
  watch: {
    percent(newPercent, oldPercent) {
      if (this.active) {
        clearInterval(this.interval)
        if (this.activeMode === 'backwards') {
          this.animationPercent = 0
          this.interval = setInterval(() => {
            this.animationPercent++
          }, this.duration)
        } else if (this.activeMode === 'forwards') {
          if (newPercent > oldPercent) {
            this.interval = setInterval(() => {
              this.animationPercent++
            }, this.duration)
          } else if (newPercent < oldPercent) {
            this.interval = setInterval(() => {
              this.animationPercent--
            }, this.duration)
          }
        }
      }
    },
    animationPercent() {
      if (this.animationPercent === this.percent) {
        clearInterval(this.interval)
        this.$emit('activeend')
      }
    }
  },
  data() {
    return {
      animationPercent: 0,
      interval: undefined
    }
  },
  render(h) {
    return h('wx-progress', [
      h('div', {
        class: [
          'wx-progress-bar'
        ],
        style: {
          borderRadius: `${this.borderRadius}px`,
          backgroundColor: this.backgroundColor,
          height: `${this.strokeWidth}px`
        },
        attrs: {
          role: 'progressbar',
          'aria-valuenow': `${this.active ? this.animationPercent : this.percent}%`
        }
      }, [
        h('div', {
          class: [
            'wx-progress-inner-bar'
          ],
          style: {
            width: `${this.active ? this.animationPercent : this.percent}%`,
            backgroundColor: this.activeColor
          }
        })
      ]),
      h('p', {
        class: [
          'wx-progress-info'
        ],
        style: {
          fontSize: `${this.fontSize}px`
        },
        attrs: {
          hidden: !this.showInfo
        }
      }, `${this.active ? this.animationPercent : this.percent}%`)
    ])
  }
}
Vue.component('x-progress', Progress)
