const View = {
  props: {
    hoverClass: {
      type: String,
      default: ''
    },
    hoverStopPropagation: {
      type: Boolean,
      default: false
    },
    hoverStartTime: {
      type: Number,
      default: 50
    },
    hoverStayTime: {
      type: Number,
      default: 400
    }
  },
  data() {
    return {
      isActive: false,
      isMoving: false,
      hoverStartTimer: undefined,
      hoverStayTimer: undefined
    }
  },
  methods: {
    touchstart(e) {
      this.isMoving = false
      if (this.hoverStopPropagation) {
        e.stopPropagation()
      }
      this.hoverStartTimer = setTimeout(() => {
        this.isActive = true
      }, this.hoverStartTime)
    },
    touchmove(e) {
      if (this.hoverStopPropagation) {
        e.stopPropagation()
      }
      clearTimeout(this.hoverStayTimer)
      clearTimeout(this.hoverStartTimer)
      this.isActive = false
      this.isMoving = true
    },
    touchend(e) {
      if (this.hoverStopPropagation) {
        e.stopPropagation()
      }
      clearTimeout(this.hoverStartTimer)
      this.hoverStayTimer = setTimeout(() => {
        this.isActive = false
      }, this.hoverStayTime)
      if (!this.isMoving) {
        this.$emit('tap')
      }
    }
  },
  render(h) {
    return h('wx-view', {
      class: [
        `${this.isActive ? this.hoverClass : ''}`
      ],
      on: {
        touchstart: this.touchstart,
        touchend: this.touchend,
        touchmove: this.touchmove
      }
    }, this.$slots.default)
  }
}
Vue.component('x-view', View)
