const PickerView = {
  props: {
    value: {
      validator: function (value) {
        return this.isValidValue(value)
      }
    },
    indicatorStyle: {
      type: String
    },
    indicatorClass: {
      type: String
    },
    maskStyle: {
      type: String
    },
    maskClass: {
      type: String
    }
  },
  data() {
    return {
      formatValue: [],
      defaultIndicatorStyle: 'height: 34px; top: 133px;',
      defaultMaskStyle: 'background-size: 100% 133px;'
    }
  },
  watch: {
    value: {
      deep: true,
      immediate: true,
      handler: function(newVal, oldVal) {
        this.formatValue = newVal.map(item => item)
      }
    }
  },
  methods: {
    emitPickstart() {
      this.$emit('pickstart')
    },
    emitPickend() {
      this.$emit('pickend')
    },
    changeFormatValue(index, value, propagate) {
      this.formatValue[index] = value
      if (propagate) {
        this.$emit('change', {
          detail: {
            value: this.formatValue
          }
        })
      }
    },
    isValidValue(value) {
      if (Array.isArray(value)) {
        return value.reduce((total, item) => total && !Number.isNaN(Number.parseInt(item)), true)
      } else {
        return false
      }
    }
  },
  computed: {
    childrenAmount() {
      return this.$children.filter(child => child.$options.name === 'x-picker-view-column').length
    },
    computedIndicatorStyle() {
      return this.defaultIndicatorStyle + ( this.indicatorStyle || '' )
    },
    computedMaskStyle() {
      return this.defaultMaskStyle + ( this.maskStyle || '' )
    }
  },
  provide () {
    return {
      pickerViewContext: this
    }
  },
  render(h) {
    return h('wx-picker-view', {}, [
      h('div', {
        class: {
          wrapper: true
        },
        style: {
          height: '100%'
        }
      }, this.$slots.default)
    ])
  }
}
Vue.component('x-picker-view', PickerView)
