const Image = {
  template: '<wx-image v-bind:src="src"><div role="img" aria-label :style="`${imageStyle}background-repeat: no-repeat; background-image:url(${src})`"></div><span></span></wx-image>',
  props: {
    src: {
      type: String
    },
    mode: {
      type: String,
      default: 'scaleToFill'
    },
    webp: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    imageStyle () {
      if (this.mode === 'scaleToFill') {
        return 'background-size: 100% 100%; background-position: 0% 0%;'
      }
      if (this.mode === 'aspectFit') {
        return 'background-size: contain; background-position: center center;'
      }
      if (this.mode === 'aspectFill') {
        return 'background-size: cover; background-position: center center;'
      }
      if (this.mode === 'top') {
        return 'background-size: auto; background-position: center top;'
      }
      if (this.mode === 'bottom') {
        return 'background-size: auto; background-position: center bottom;'
      }
      if (this.mode === 'center') {
        return 'background-size: auto; background-position: center center;'
      }
      if (this.mode === 'left') {
        return 'background-size: auto; background-position: center left;'
      }
      if (this.mode === 'right') {
        return 'background-size: auto; background-position: center right;'
      }
      if (this.mode === 'top left') {
        return 'background-size: auto; background-position: left top;'
      }
      if (this.mode === 'top right') {
        return 'background-size: auto; background-position: right top;'
      }
      if (this.mode === 'bottom left') {
        return 'background-size: auto; background-position: left bottom;'
      }
      if (this.mode === 'bottom right') {
        return 'background-size: auto; background-position: right bottom;'
      }
      return ''
    }
  }
}
Vue.component('x-image', Image)
