const easingFunctions = {
  default: 'ease',
  linear: 'linear',
  easeInCubic: 'ease-in',
  easeOutCubic: 'ease-out',
  easeInOutCubic: 'ease-in-out'
}
const Swiper = {
  props: {
    indicatorDots: {
      type: Boolean,
      default: false
    },
    indicatorColor: {
      type: String,
      default: 'rgba(0,0,0, .3)'
    },
    interval: {
      type: Number,
      default: 5000
    },
    duration: {
      type: Number,
      default: 500
    },
    circular: {
      type: Boolean,
      default: false
    },
    vertical: {
      type: Boolean,
      default: false
    },
    previousMargin: {
      type: Number,
      default: 0
    },
    nextMargin: {
      type: Number,
      default: 0
    },
    displayMultipleItems: {
      type: Number,
      default: 1
    },
    current: {
      type: Number,
      default: 0
    },
    autoplay: {
      type: Boolean,
      default: false
    },
    skipHiddenItemLayout: {
      type: Boolean,
      default: false
    },
    easingFunction: {
      type: String,
      default: 'easeOutCubic'
    }
  },
  data() {
    return {
      startX: null,
      startY: null,
      currentX: null,
      currentY: null,
      offsetX: 0,
      offsetY: 0,
      percent: 0,
      currentIndex: this.current,
      transition: null,
      intervalTimer: undefined
    }
  },
  mounted() {
    this.play()
  },
  watch: {
    autoplay() {
      if (this.autoplay) {
        this.play()
      } else {
        clearInterval(this.intervalTimer)
      }
    },
    currentIndex() {
      const childTag = 'x-swiper-item'
      const children = this.$slots.default.filter(item => {
        if (item.tag && item.tag.indexOf(childTag)) {
          return item
        }
      })
      if (this.circular) {
        if (this.currentIndex !== -1 && this.currentIndex !== children.length) {
          this.$emit('change', this.currentIndex)
        }
      } else {
        this.$emit('change', this.currentIndex)
      }
    }
  },
  methods: {
    touchStart(e) {
      this.transition = null
      this.startX = e.touches[0].clientX
      this.startY = e.touches[0].clientY
      clearInterval(this.intervalTimer)
    },
    touchMove(e) {
      const frameWidth = this.$refs.frame.clientWidth
      const frameHeight = this.$refs.frame.clientHeight
      this.currentX = e.touches[0].clientX
      this.currentY = e.touches[0].clientY
      let moveX = this.currentX - this.startX
      let moveY = this.currentY - this.startY
      this.percent = this.vertical ? moveY / frameHeight : moveX / frameWidth
      this.offsetX = - this.currentIndex * 100 + this.percent * 100
      this.offsetY = - this.currentIndex * 100 + this.percent * 100
    },
    touchEnd(e) {
      let page = this.percent > 0 ? -1 : 1
      const childTag = 'x-swiper-item'
      const children = this.$slots.default.filter(item => {
        if (item.tag && item.tag.indexOf(childTag)) {
          return item
        }
      })
      const min = this.circular ? -1 : 0
      const max = this.circular ? children.length : children.length - 1
      this.transition = `transform ${this.duration / 1000}s ${easingFunctions[this.easingFunction]}`
      let switchTimer = undefined
      if (Math.abs(this.percent) > 0.4) {
        if ((this.currentIndex + page) >= min && (this.currentIndex + page) <= max) {
          this.currentIndex = this.currentIndex + page
          if (this.circular && this.currentIndex === max) {
            switchTimer = setTimeout(() => {
              this.transition = null
              this.currentIndex = min + 1
              this.offsetX = - this.currentIndex * 100
              this.offsetY = - this.currentIndex * 100
            }, this.duration)
          }
          if (this.circular && this.currentIndex === min) {
            switchTimer = setTimeout(() => {
              this.transition = null
              this.currentIndex = max - 1
              this.offsetX = - this.currentIndex * 100
              this.offsetY = - this.currentIndex * 100
            }, this.duration)
          }
        }
      }
      this.offsetX = - this.currentIndex * 100
      this.offsetY = - this.currentIndex * 100
      this.play()
    },
    play() {
      const childTag = 'x-swiper-item'
      const children = this.$slots.default.filter(item => {
        if (item.tag && item.tag.indexOf(childTag)) {
          return item
        }
      })
      if (this.autoplay) {
        let switchTimer = undefined
        this.intervalTimer = setInterval(() => {
          if (this.currentIndex < (children.length - 1)) {
            clearTimeout(switchTimer)
            this.transition = `transform ${this.duration / 1000}s ${easingFunctions[this.easingFunction]}`
            this.currentIndex++
          } else {
            if (this.circular) {
              this.currentIndex = this.currentIndex + 1
              const switchTimer = setTimeout(() => {
                this.currentIndex = 0
                this.transition = null
                this.offsetX = - this.currentIndex * 100
                this.offsetY = - this.currentIndex * 100
              }, this.duration)
            } else {
              this.currentIndex = 0
            }
          }
          this.offsetX = - this.currentIndex * 100
          this.offsetY = - this.currentIndex * 100
        }, this.interval)
      }
    }
  },
  render(h) {
    const childTag = 'x-swiper-item'
    const children = this.$slots.default.filter(item => {
      if (item.tag && item.tag.indexOf(childTag)) {
        return item
      }
    })
    let hiddenIndex
    if (this.currentIndex === 0) {
      hiddenIndex = children.length - 1
    }
    if (this.currentIndex === children.length - 1) {
      hiddenIndex = 0
    }
    const childrenArray = children.map((item, index) => {
      return h('wx-swiper-item', {
        style: {
          position: 'absolute',
          width: '100%',
          height: '100%',
          transform: `translate(${this.vertical ? 0 : index * 100 + '%'}, ${this.vertical ? index * 100 + '%': 0}) translateZ(0px)`,
          display: `${this.skipHiddenItemLayout ? (index === hiddenIndex ? 'none' : '') : null}`
        }
      }, item.componentOptions.children)
    })
    if (this.circular) {
      if (this.currentIndex === 0 || this.currentIndex === -1) {
        childrenArray[childrenArray.length - 1].data.style.transform = `translate(${this.vertical ? 0 : -100}%, ${this.vertical ? -100 + '%' : 0}) translateZ(0px)`
      }
      if (this.currentIndex === (childrenArray.length - 1) || this.currentIndex === childrenArray.length) {
        childrenArray[0].data.style.transform = `translate(${this.vertical ? 0 : childrenArray.length * 100 + '%'}, ${this.vertical ? childrenArray.length * 100 + '%' : 0}) translateZ(0px)`
      }
    }
    let indicator
    const dots = children.map((item, index) => {
      let currentIndex = this.currentIndex
      if (this.circular) {
        if (this.currentIndex === children.length) {
          currentIndex = 0
        }
        if (this.currentIndex === -1) {
          currentIndex = children.length - 1
        }
      }
      return h('div', {
        attrs: {
          dataDotIndex: index
        },
        class: [
          'wx-swiper-dot',
          {
            'wx-swiper-dot-active': index === currentIndex
          }
        ]
      })
    })

    if (this.indicatorDots) {
      indicator = h('div', {
        class: [
          'wx-swiper-dots',
          {
            'wx-swiper-dots-horizontal': !this.vertical,
            'wx-swiper-dots-vertical': this.vertical
          }
        ]
      }, dots)
    }

    return h('wx-swiper', {}, [
      h('div', {
        class: 'wx-swiper-wrapper',
        attrs: {
          role: 'listbox',
          data: this.currentIndex
        },
        on: {
          touchstart: this.touchStart,
          touchmove: this.touchMove,
          touchend: this.touchEnd
        }
      }, [
        h('div', {
          class: 'wx-swiper-slides',
          style: {
            marginLeft: `${this.previousMargin}px`,
            marginRight: `${this.nextMargin}px`
          }
        }, [
          h('div', {
            class: 'wx-swiper-slide-frame',
            style: {
              transition: this.transition,
              height: '100%',
              width: `${100 / this.displayMultipleItems}%`,
              transform: `translate(${this.vertical ? 0 : this.offsetX + '%'}, ${this.vertical ? this.offsetY + '%' : 0}) translateZ(0px)`,
            },
            ref: 'frame'
          }, childrenArray)
        ]),
        indicator
      ])
    ])
  }
}
Vue.component('x-swiper', Swiper)
