const ScrollView = {
  props: {
    scrollX: {
      type: Boolean
    },
    scrollY: {
      type: Boolean
    },
    scrollTop: {
      type: Number
    },
    scrollLeft: {
      type: Number
    },
    upperThreshold: {
      type: Number,
      default: 50
    },
    lowerThreshold: {
      type: Number,
      default: 50
    },
    scrollIntoView: {
      type: String
    },
    scrollWithAnimation: {
      type: Boolean
    }
  },
  data() {
    return {
      height: 0,
      upperEmmiter: true,
      lowerEmmiter: true,
      elements: [],
      view: undefined,
      timmer: undefined,
      max: undefined,
      isAutoScrolling: false
    }
  },
  mounted() {
    const children = this.$slots.default.filter(item => { return item.tag !== undefined && item.text !== ' ' })
    this.height = children[0].elm.offsetHeight
    this.scroll(this.scrollTop, 'upper')
    this.$refs.scroller.scrollLeft = this.scrollLeft
    children.forEach((element, index) => {
      this.elements.push({
        id: element.data && element.data.attrs && element.data.attrs.id ? element.data.attrs.id : index,
        index: index,
        offsetTop: element.elm.offsetTop,
        offsetLeft: element.elm.offsetLeft
      })
    });
    const element = this.elements.find(item =>
      item.id === this.scrollIntoView
    )
    this.view = element
    if (this.scrollIntoView) {
      if (this.scrollY) {
        this.scroll(element.offsetTop, 'upper')
      }
      if (this.scrollX) {
        this.scroll(element.offsetTop, 'upper')
      }
    }
  },
  watch: {
    scrollTop(newValue, oldValue) {
      let direction
      if (this.scrollY) {
        if (newValue > oldValue) {
          direction = 'upper'
          this.scroll(this.scrollTop, direction)
        }
        if (newValue < oldValue) {
          direction = 'lower'
          this.scroll(this.scrollTop, direction)
        }
      }
    },
    scrollLeft(newValue, oldValue) {
      let direction
      if (this.scrollX) {
        if (newValue > oldValue) {
          direction = 'upper'
          this.scroll(this.scrollTop, direction)
        }
        if (newValue < oldValue) {
          direction = 'lower'
          this.scroll(this.scrollTop, direction)
        }
      }
    },
    scrollIntoView() {
      if (this.scrollIntoView) {
        let direction
        const lastIndex = this.view.index
        const element = this.elements.find(item => item.id === this.scrollIntoView)

        if (this.scrollY) {
          if (element.index < lastIndex) {
            direction = 'lower'
          }
          if (element.index > lastIndex) {
            direction = 'upper'
          }
          if (this.$refs.scroller.scrollTop < this.elements[lastIndex].offsetTop) {
            direction = 'upper'
          }
          if (this.$refs.scroller.scrollTop > this.elements[lastIndex].offsetTop) {
            direction = 'lower'
          }
          this.scroll(element.offsetTop, direction)
        }
        if (this.scrollX) {
          this.$refs.scroller.scrollLeft = element.offsetLeft
        }
      }
    }
  },
  computed: {
    scrollerText() {
      if (this.scrollY) {
        return '可上下滚动'
      }
      if (this.scrollX) {
        return '可左右滚动'
      }
    },
    wrapperStyle() {
      if (this.scrollX) {
        return `height: ${this.height + 5}px; overflow-y: hidden;`
      }
      if (this.scrollY) {
        return `height: 100%; overflow-x: hidden;`
      }
      return ''
    },
    scrollerStyle() {
      if (this.scrollY) {
        return 'overflow: hidden auto;'
      }
      if (this.scrollX) {
        return 'overflow: auto hidden;padding-bottom: 20px;'
      }
      return ''
    }
  },
  render(h) {
    const children = this.$slots.default.filter(item => { return item.tag !== undefined && item.text !== ' ' })
    return h('wx-scroll-view', {
    }, [
      h('div', {
        class: 'wx-scroll-view',
        style: this.wrapperStyle,
        attrs: {
          role: 'listbox',
          'aria-label': this.scrollerText,
        }
      }, [
        h('div', {
          class: 'wx-scroll-view',
          style: this.scrollerStyle,
          on: {
            scroll: this.onScroll
          },
          ref: 'scroller'
        }, [
          h('div', children)
        ])
      ])
    ])
  },
  methods: {
    onScroll(e) {
      if (this.scrollY) {
        // approach top area
        if (this.$refs.scroller.scrollTop > this.upperThreshold) {
          this.upperEmmiter = true
        }
        // approach bottom area
        if (this.$refs.scroller.scrollTop < this.$refs.scroller.scrollHeight - this.$refs.scroller.clientHeight - this.lowerThreshold) {
          this.lowerEmmiter = true
        }

        if (this.$refs.scroller.scrollTop <= this.upperThreshold && this.upperEmmiter) {
          this.$emit('scrolltoupper', {
            type: 'scrolltoupper',
            detail: {
              direction: 'top'
            }
          })
          this.upperEmmiter = false
        }
        if ((this.$refs.scroller.scrollTop >= this.$refs.scroller.scrollHeight - this.$refs.scroller.clientHeight - this.lowerThreshold) && this.lowerEmmiter) {
          this.$emit('scrolltolower', {
            type: 'scrolltolower',
            detail: {
              direction: 'bottom'
            }
          })
          this.lowerEmmiter = false
        }
        this.$emit('scroll', {
          type: 'scroll',
          detail: {
            scrollHeight: this.$refs.scroller.scrollHeight,
            scrollLeft: this.$refs.scroller.scrollLeft,
            scrollTop: this.$refs.scroller.scrollTop,
            scrollWidth: this.$refs.scroller.scrollWidth,
          }
        })
      }
      if (this.scrollX) {
        if (this.$refs.scroller.scrollLeft > this.upperThreshold) {
          this.upperEmmiter = true
        }

        if (this.$refs.scroller.scrollLeft < this.$refs.scroller.scrollWidth - this.$refs.scroller.clientWidth - this.lowerThreshold) {
          this.lowerEmmiter = true
        }

        if (this.$refs.scroller.scrollLeft <= this.upperThreshold && this.upperEmmiter) {
          this.$emit('scrolltoupper', {
            type: 'scrolltoupper',
            detail: {
              direction: 'left'
            }
          })
          this.upperEmmiter = false
        }
        if ((this.$refs.scroller.scrollLeft >= this.$refs.scroller.scrollWidth - this.$refs.scroller.clientWidth - this.lowerThreshold) && this.lowerEmmiter) {
          this.$emit('scrolltolower', {
            type: 'scrolltolower',
            detail: {
              direction: 'right'
            }
          })
          this.lowerEmmiter = false
        }
      }
    },
    scroll(max) {
      this.$refs.scroller.scrollTop = max
    }
  }
}
Vue.component('x-scroll-view', ScrollView)
