const Input = {
  props: {
    value: {
      type: String
      // required: true
    },
    type: {
      type: String,
      default: 'text',
      validator: (val) => {
        return ['text', 'number', 'idcard', 'digit'].indexOf(val) !== -1
      }
    },
    password: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String
      // required: true
    },
    placeholderStyle: {
      type: String
      // required: true
    },
    placeholderClass: {
      type: String,
      default: 'input-placeholder'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    maxlength: {
      type: Number,
      default: 140
    },
    cursorSpacing: {
      type: Number,
      default: 0
    },
    focus: {
      type: Boolean,
      default: false
    },
    confirmType: {
      type: String,
      default: 'done',
      validator: (val) => {
        return ['send', 'search', 'next', 'go', 'done'].indexOf(val) !== -1
      }
    },
    confirmHold: {
      type: Boolean,
      default: false
    },
    cursor: {
      type: Number
      // required: true
    },
    selectionStart: {
      type: Number,
      default: -1
    },
    selectionEnd: {
      type: Number,
      default: -1
    },
    adjustPosition: {
      type: Boolean,
      default: true
    },
    holdKeyboard: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isShowInput: true,
      currentValue: this.value || '',
      inputHeight: '',
      inputInfo: {},
    }
  },
  watch: {
    value(newVal, oldVal) {
      this.currentValue = newVal
      this.onFocus()
    }
  },
  computed: {
    currentPlaceholderStyle() {
      if (this.currentValue !== '') {
        return 'display:none'
      }
      return `height: ${this.inputHeight}; line-height: ${this.inputHeight};`
    },
    currentType() {
      if (this.password) {
        return 'password'
      }
      return 'text'
    },
    showPlaceholder() {
      return !!!this.currentValue.length
    }
  },
  mounted() {
    const inputId = serviceBridge.subscribe('MP-input-input', (res) => {
      if (Number.parseInt(res.uid) === Number.parseInt(this._uid)) {
        this.currentValue = res.value
        this.$emit('input', {
          detail: {
            value: this.currentValue,
            cursor: res.cursor,
            keyCode: res.keyCode
          }
        })
      }
    })
    const blurId = serviceBridge.subscribe('MP-input-blur', (res) => {
      if (Number.parseInt(res.uid) === Number.parseInt(this._uid)) {
        this.currentValue = res.value
        this.isShowInput = true
        this.$emit('blur', {
          detail: {
            value: this.currentValue
          }
        })
      }
    })
    const confirmId = serviceBridge.subscribe('MP-input-confirm', (res) => {
      if (Number.parseInt(res.uid) === Number.parseInt(this._uid)) {
        this.currentValue = res.value
        this.$emit('confirm', {
          detail: {
            value: this.currentValue
          }
        })
      }
    })
    // console.log(inputId, blurId, confirmId)
    this.inputHeight = window.getComputedStyle(this.$refs.inputWrapper).height
    const ele = this.$refs.input
    // const rect = this.$refs.input.getBoundingClientRect().toJSON()
    const rect = this.getCoordinate(ele)
    this.inputInfo = {
      value: this.currentValue,
      type: this.type,
      password: this.password,
      maxlength: this.maxlength,
      cursorSpacing: this.cursorSpacing,
      focus: this.focus,
      confirmType: this.confirmType,
      confirmHold: this.confirmHold,
      cursor: this.cursor,
      selectionStart: this.selectionStart,
      selectionEnd: this.selectionEnd,
      adjustPosition: this.adjustPosition,
      holdKeyboard: this.holdKeyboard,
      x: rect.x,
      y: rect.y,
      width: rect.width,
      height: rect.height,
      uid: this._uid
    }
    if (this.focus) {
      ele.focus()
    }
  },
  methods: {
    getCoordinate(ele) {
      let left = ele.offsetLeft
      let top = ele.offsetTop
      let parent = ele.offsetParent
      while (parent !== null) {
        left += parent.offsetLeft
        top += parent.offsetTop
        parent = parent.offsetParent
      }

      return {
        x: left,
        y: top,
        width: ele.clientWidth,
        height: ele.clientHeight
      }
    },
    onFocus() {
      this.inputInfo.value = this.currentValue
      this.inputInfo.type = this.type
      this.inputInfo.password = this.password
      this.inputInfo.maxlength = this.maxlength
      this.inputInfo.cursorSpacing = this.cursorSpacing
      this.inputInfo.focus = this.focus
      this.inputInfo.confirmType = this.confirmType
      this.inputInfo.confirmHold = this.confirmHold
      this.inputInfo.cursor = this.cursor
      this.inputInfo.selectionStart = this.selectionStart
      this.inputInfo.selectionEnd = this.selectionEnd
      this.inputInfo.adjustPosition = this.adjustPosition
      this.inputInfo.holdKeyboard = this.holdKeyboard
      Bridge.Component.showInput(Object.assign({
        success: (res) => {
          this.$refs.input.blur()
          this.isShowInput = false
          this.$emit('focus', {
            value: this.currentValue,
            height: res.height
          })
        }
      }, this.inputInfo))
    }
  },
  render(h) {
    return h('wx-input', {
      ref: 'inputWrapper'
    }, [
      h('div', {}, [
        this.isShowInput && h('input', {
          style: {
            height: this.inputHeight,
            lineHeight: this.inputHeight,
            color: 'rgb(0,0,0)'
          },
          attrs: {
            type: this.currentType,
            value: this.currentValue,
            disabled: this.disabled ? 'disabled' : null,
            maxlength: ( Number.isNaN(this.maxlength) || this.maxlength === -1 ) ? null : this.maxlength
          },
          on: {
            focus: this.onFocus
          },
          ref: 'input'
        }),
        this.showPlaceholder && h('div', {
          class: this.placeholderClass,
          style: this.currentPlaceholderStyle + this.placeholderStyle
        }, [
          this.placeholder
        ])
      ])
    ])
  }
}
Vue.component('x-input', Input)
