const PickerViewColumn = {
  inject: {
    pickerViewContext: {default: undefined}
  },
  mounted() {
    this.pickerViewHeight = window.getComputedStyle(this.findPickerView(this).$el).height
    // this.pickerViewColumnWidth = window.getComputedStyle(this.$el).width
    this.indexInPickerView = this.findPickerView(this).$children.findIndex(item => item._uid === this._uid)
    const chosenIndexValue = this.pickerViewContext.value[this.indexInPickerView]
    if (chosenIndexValue) {
      if (chosenIndexValue > this.$slots.default.length) {
        this.pickerViewContext.changeFormatValue(this.indexInPickerView, this.$slots.default.length - 1, false)
      } else {
        this.pickerViewContext.changeFormatValue(this.indexInPickerView, chosenIndexValue, false)
      }
    } else {
      this.pickerViewContext.changeFormatValue(this.indexInPickerView, 0, false)
    }
    const indicatorHeightStr = window.getComputedStyle(this.$refs.indicator).height
    this.indicatorHeight = Number.parseInt(indicatorHeightStr.substr(0, indicatorHeightStr.length - 1))
    const indicatorTop = ( Number.parseInt(this.pickerViewHeight.substr(0, indicatorHeightStr.length - 1)) - this.indicatorHeight ) / 2
    this.$refs.content.style.paddingTop = `${indicatorTop}px`
    this.$refs.content.style.paddingBottom = `${indicatorTop}px`
    this.$refs.mask.style.backgroundSize= `100% ${indicatorTop}px`
    this.$refs.indicator.style.top= `${indicatorTop}px`
    // this.$refs.mask.style.top = `${indicatorTop}px`
    this.pickerViewColumnTransformY = -(this.indicatorHeight) * this.pickerViewContext.formatValue[this.indexInPickerView]
    let slotsDefault = this.$slots.default.filter(item => !!item.tag)
    slotsDefault.forEach((item, index) => {
      item.elm.onclick = () => {
        this.enableTransition = true
        if (this.pickerViewContext) {
          this.pickerViewContext.emitPickstart()
        }
        this.pickerViewContext.changeFormatValue(this.indexInPickerView, index, true)
        this.pickerViewColumnTransformY = -(this.indicatorHeight) * this.pickerViewContext.formatValue[this.indexInPickerView]
        if (this.pickerViewContext) {
          this.pickerViewContext.emitPickend()
        }
        setTimeout(() => {
          this.enableTransition = false
        }, 200)
      }
      // item.elm.style.transform = `rotateX(${25 * (this.pickerViewContext.formatValue[this.indexInPickerView] - index)}deg)`
      if (!item.elm.style) {
        item.elm.style = {}
      }
      item.elm.style['transition-duration'] = '0ms'
      item.elm.style['transition-timing-function'] = 'cubic-bezier(0.23, 1, 0.32, 1)'
      item.elm.style.height = `${this.indicatorHeight}px`
      item.elm.style.overflow = 'hidden'
    })
    // console.log(this.findPickerView(this).$children.filter(child => child.$options.name === 'x-picker-view-column').length)
  },
  render(h) {
    let slotsDefault = this.$slots.default.filter(item => !!item.tag)
    return h('wx-picker-view-column', {
      class: {
        'wx-picker__group': true
      },
      style: {
        height: this.pickerViewHeight
      }
    }, [
      h('div', {
        ref: 'mask',
        class: [
          'wx-picker__mask',
          this.pickerViewContext.maskClass
        ],
        style: this.pickerViewContext.computedMaskStyle
      }),
      h('div', {
        ref: 'indicator',
        class: [
          'wx-picker__indicator',
          this.pickerViewContext.indicatorClass
        ],
        style: this.pickerViewContext.computedIndicatorStyle
      }),
      h('div', {
        ref: 'content',
        class: [
          'wx-picker__content'
        ],
        on: {
          touchstart: (e) => {
            if (this.pickerViewContext) {
              this.pickerViewContext.emitPickstart()
            }
            this.offsetY = 0
            this.startY = e.touches[0].pageY
            e.preventDefault()
          },
          touchmove: (e) => {
            let totalY = e.touches[0].pageY - this.startY
            let y = totalY - this.offsetY
            this.pickerViewColumnTransformY += y
            this.offsetY = totalY
            e.preventDefault()
          },
          touchend: (e) => {
            let result = this.pickerViewColumnTransformY
            let reusltIndex = 0
            if (result > 0) {
              this.pickerViewColumnTransformY = 0
              reusltIndex = 0
            } else if (result < -(this.indicatorHeight * (slotsDefault.length - 1))) {
              this.pickerViewColumnTransformY = -(this.indicatorHeight * (slotsDefault.length - 1))
              reusltIndex = slotsDefault.length - 1
            } else {
              const index = Math.round(Math.abs(this.pickerViewColumnTransformY) / this.indicatorHeight)
              reusltIndex = index
              this.pickerViewColumnTransformY = -(this.indicatorHeight * index)
            }
            if (this.offsetY) {
              this.pickerViewContext.changeFormatValue(this.indexInPickerView, reusltIndex, true)
            }
            this.offsetY = 0
            if (this.pickerViewContext) {
              this.pickerViewContext.emitPickend()
            }
            e.preventDefault()
          }
        },
        style: [
          {
            transition: this.enableTransition ? '-webkit-transform 0.2s ease-out 0s' : 'unset',
            // padding: `${this.pickerViewColumnWidth} 0px`,
            transform: `translateY(${this.pickerViewColumnTransformY}px) translateZ(0px)`
          }
        ]
      }, slotsDefault)
    ])
  },
  data() {
    return {
      children: [],
      startY: 0,
      offsetY: 0,
      enableTransition: false,
      indicatorHeight: 0,
      pickerViewHeight: 'noset',
      pickerViewColumnTransformY: 0,
      // pickerViewColumnWidth: null,
      indexInPickerView: null
    }
  },
  watch: {
    'pickerViewContext.formatValue': function() {
      this.pickerViewColumnTransformY = -(this.indicatorHeight) * this.pickerViewContext.formatValue[this.indexInPickerView]
    }
  },
  methods: {
    findPickerView(vnode) {
      if (vnode.$options.name === 'x-picker-view') {
        return vnode
      } else {
        return this.findPickerView(vnode.$parent)
      }
    }
  }
}
Vue.component('x-picker-view-column', PickerViewColumn)
