const Checkbox = {
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    color: {
      type: String,
      default: '#09BB07'
    },
    value: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  inject: {
    checkboxGroupContext: {default: undefined}
  },
  data () {
    return {
      lChecked: false
    }
  },
  mounted () {
    this.change(false)
  },
  watch: {
    checked: {
      immediate: true,
      handler: function(newVal, oldVal) {
        this.lChecked = newVal
      }
    },
    // 待确定
    'checkboxGroupContext.value': function(newVal, oldVal) {
      this.lChecked = newVal.indexOf(this.value) > -1 ? true : false
    }
  },
  render(h) {
    return h('wx-checkbox', {
      on: {
        click: (event) => {
          event.stopPropagation()
          if (!this.disabled) {
            this.changeState()
            this.change(true)
          }
        }
      }
    }, [
      h('div', {
        class: {
          'wx-checkbox-wrapper': true
        }
      }, [
        h('div', {
          style: {
            color: this.color
          },
          class: {
            'wx-checkbox-input': true,
            // 'wx-checkbox-input-checked': this.checkboxGroupContext ? !!this.lChecked : !!this.checked,
            'wx-checkbox-input-checked': !!this.lChecked,
            'wx-checkbox-input-disabled': !!this.disabled
          }
        }),
        this.$slots.default
      ])
    ])
  },
  methods: {
    changeState() {
      this.lChecked = !this.lChecked
    },
    change(propagate) {
      if (this.checkboxGroupContext && this.checkboxGroupContext.change) {
        let copy = this.checkboxGroupContext.value.map(item => item)
        if (this.lChecked) {
          copy.push(this.value)
        } else {
          copy = copy.filter((value) => value !== this.value)
        }
        this.checkboxGroupContext.change(copy, propagate)
      }
    },
    reverse() {
      console.log('reverse checkbox')
      this.lChecked = !this.lChecked
    }
  }
}
Vue.component('x-checkbox', Checkbox)
