import FormChild from '../../mixins/FormChild'
const Slider = {
  props: {
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    },
    step: {
      type: Number,
      default: 1
    },
    disabled: {
      type: Boolean,
      default: false
    },
    value: {
      type: Number,
      default: 0
    },
    activeColor: {
      type: String,
      default: 'rgb(26, 173, 25)'
    },
    backgroundColor: {
      type: String,
      default: 'rgb(233,233,233)'
    },
    blockSize: {
      type: Number,
      default: 28
    },
    blockColor: {
      type: String,
      default: 'rgb(255,255,255)'
    },
    showValue: {
      type: Boolean,
      default: false
    }
  },
  mixins: [FormChild],
  inject: {
    formContext: {default: undefined}
  },
  data() {
    return {
      currentValue: this.value,
      offsetLeft: (this.value - this.min) / (this.max - this.min) * 100,
      isDragging: false,
      startX: null,
      currentX: null
    }
  },
  watch: {
    'formContext.reset': function(val) {
      this.currentValue = 0
      this.offsetLeft = 0 // 待确定
    }
  },
  mounted () {
    if (this.value < this.min) {
      this.currentValue = this.min
    }
    this.tellForm(this.currentValue)
  },
  methods: {
    dragStart(e) {
      this.isDragging = true
      this.startX = e.touches[0].clientX
    },
    drag(e) {
      if (this.isDragging && !this.disabled) {
        this.currentX = e.touches[0].clientX
        const diff = Math.round((this.currentX - this.startX) / this.$refs.area.offsetWidth * 100)
        let direction
        if (diff > 0) {
          direction = 1
        } else if (diff < 0) {
          direction = -1
        } else {
          direction = 0
        }

        if (this.offsetLeft + diff < 0) {
          this.offsetLeft = 0
        } else if (this.offsetLeft + diff > 100) {
          this.offsetLeft = 100
        } else {
          this.offsetLeft = this.offsetLeft + diff
        }
        this.startX = e.touches[0].clientX
        this.currentValue = Math.round((Math.round(this.offsetLeft / 100 * (this.max - this.min)) + this.min) / this.step) * this.step
        this.$emit('changing', this.currentValue)
      }
    },
    dragEnd() {
      this.$emit('change', this.currentValue)
      this.isDragging = false
      this.tellForm(this.currentValue)
    },
    chooseStart(e) {
      if (!this.isDragging && !this.disabled) {
        e.preventDefault()
        let x = e.touches[0].clientX
        const left = this.$refs.area.offsetLeft
        const right = this.$refs.area.offsetLeft + this.$refs.area.offsetWidth
        if (e.touches[0].clientX < left) {
          x = left
        }
        if (e.touches[0].clientX > right) {
          x = right
        }
        const offset = Math.round((x - left) / this.$refs.area.offsetWidth * 100)
        this.offsetLeft = Math.round(offset / this.step) * this.step
        this.currentValue = Math.round(this.offsetLeft / 100 * (this.max - this.min)) + this.min
      }
    },
    chooseEnd(e) {
      this.tellForm(this.currentValue)
    },
  },
  render(h) {
    return h('wx-slider', {
      attrs: {
        value: this.currentValue
      }
    }, [
      h('div', {
        class: 'wx-slider-wrapper',
        attrs: {
          role: 'slider',
          'aria-valuemax': this.max,
          'aria-valuemin': this.min,
          'aria-valuenow': this.currentValue
        }
      }, [
        h('div', {
          class: 'wx-slider-tap-area',
          ref: 'area',
          on: {
            touchstart: this.chooseStart,
            touchend: this.chooseEnd
          }
        }, [
          h('div', {
            style: {
              backgroundColor: this.backgroundColor
            },
            class: 'wx-slider-handle-wrapper',
          }, [
            h('div', {
              style: {
                left: `${this.offsetLeft}%`
              },
              class: 'wx-slider-handle',
              on: {
                touchstart: this.dragStart,
                touchmove: this.drag,
                touchend: this.dragEnd
              },
              ref: 'handle'
            }),
            h('div', {
              style: {
                left: `${this.offsetLeft}%`,
                width: `${this.blockSize}px`,
                height: `${this.blockSize}px`,
                marginLeft: `-${this.blockSize / 2}px`,
                marginTop: `-${this.blockSize / 2}px`,
                backgroundColor: this.blockColor
              },
              class: 'wx-slider-thumb'
            }),
            h('div', {
              style: {
                width: `${this.offsetLeft}%`,
                backgroundColor: this.activeColor
              },
              class: 'wx-slider-track'
            }),
            h('div', {
              class: 'wx-slider-step'
            })
          ])
        ]),
        h('span', {
          class: 'wx-slider-value',
          attrs: {
            hidden: !this.showValue
          }
        }, this.currentValue)
      ])
    ])
  }
}
Vue.component('x-slider', Slider)
