import FormChild from '../../mixins/FormChild'
const Switch = {
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'switch'
    },
    color: {
      type: String,
      default: 'rgb(4, 190, 2)'
    },
  },
  mixins: [FormChild],
  inject: {
    formContext: {default: undefined}
  },
  data() {
    return {
      currentChecked: this.checked
    }
  },
  created() {
    this.tellForm(this.currentChecked)
  },
  methods: {
    handleCheck(e) {
      this.currentChecked = !this.currentChecked
      this.$emit('change', this.currentChecked)
      e.stopPropagation()
      this.tellForm(this.currentChecked)
    },
    reverse() {
      console.log('reverse switch')
      this.currentChecked = !this.currentChecked
      this.$emit('change', this.currentChecked)
    }
  },
  watch: {
    checked() {
      this.currentChecked = this.checked
    },
    'formContext.reset': function(val) {
      this.currentChecked = false
    }
  },
  render(h) {
    return h('wx-switch', {
    }, [
      h('div', {
        class: 'wx-switch-wrapper',
        attrs: {
          role: 'switch',
          'aria-label': true,
          'aria-checked': this.currentChecked
        }
      }, [
        h('div', {
          style: {
            backgroundColor: this.currentChecked ? this.color : undefined,
            borderColor: this.currentChecked ? this.color : undefined
          },
          attrs: {
            hidden: this.type === 'checkbox',
            type: 'checkbox'
          },
          on: {
            click: this.handleCheck
          },
          class: [
            'wx-switch-input',
            {
              'wx-switch-input-checked': this.currentChecked
            }
          ]
        }),
        h('div', {
          attrs: {
            hidden: this.type === 'switch',
            type: 'checkbox'
          },
          on: {
            click: this.handleCheck
          },
          style: {
            color: this.currentChecked ? this.color : undefined
          },
          class: [
            'wx-checkbox-input',
            {
              'wx-checkbox-input-checked': this.currentChecked
            }
          ]
        })
      ])
    ])
  }
}

Vue.component('x-switch', Switch)
