const Radio = {
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    color: {
      type: String,
      default: '#09BB07'
    },
    value: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  inject: {
    radioGroupContext: {default: undefined}
  },
  data () {
    return {
      isTouching: false,
      lChecked: false
    }
  },
  mounted () {
    this.change(false)
  },
  watch: {
    checked: {
      immediate: true,
      handler: function(newVal, oldVal) {
        this.lChecked = newVal
      }
    },
    'radioGroupContext.value': {
      handler: function(newVal, oldVal) {
        this.lChecked = newVal === this.value
      }
    }
  },
  render(h) {
    return h('wx-radio', {
      on: {
        click: (event) => {
          event.stopPropagation()
          if (!this.disabled) {
            this.changeState()
            this.change(true)
          }
        },
        touchstart: (e) => {
          this.isTouching = true
        },
        touchend: (e) => {
          if (this.isTouching) {
            this.$emit('tap', this.radioGroupContext ? !!this.lChecked : !!this.checked)
            this.isTouching = false
          }
        }
      }
    }, [
      h('div', {
        class: {
          'wx-radio-wrapper': true
        }
      }, [
        h('div', {
          style: {
            'background-color': (this.radioGroupContext ? !!this.lChecked : !!this.checked) && !this.disabled ? this.color : null,
            'border-color': (this.radioGroupContext ? !!this.lChecked : !!this.checked) && !this.disabled ? this.color : null
          },
          class: {
            'wx-radio-input': true,
            // 'wx-radio-input-checked': (this.radioGroupContext ? !!this.lChecked : !!this.checked),
            'wx-radio-input-checked': !!this.lChecked,
            'wx-radio-input-disabled': !!this.disabled
          }
        }),
        this.$slots.default
      ])
    ])
  },
  methods: {
    changeState() {
      if (!this.lChecked) {
        this.lChecked = !this.lChecked
      }
    },
    change(propagate) {
      if (this.radioGroupContext && this.radioGroupContext.change) {
        if (this.lChecked) {
          this.radioGroupContext.change(this.value, propagate && this.radioGroupContext.value !== this.value)
        } else {
        }
      }
    },
    reverse() {
      console.log('reverse radio')
      this.changeState()
    }
  }
}
Vue.component('x-radio', Radio)
