import FormChild from '../../mixins/FormChild'
const CheckboxGroup = {
  props: {
    bindchange: {
      type: Function
    }
  },
  mixins: [FormChild],
  inject: {
    formContext: {default: undefined}
  },
  watch: {
    'formContext.reset': function(val) {
      this.value = []
    }
  },
  data() {
    return {
      value: []
    }
  },
  provide () {
    return {
      checkboxGroupContext: this
    }
  },
  created() {
    this.tellForm(this.value)
  },
  render(h) {
    return h('wx-checkbox-group', {}, this.$slots.default)
  },
  methods: {
    change(data, propagate) {
      this.value = data;
      if (propagate) {
        this.$emit('change', {
          detail: {
            value: this.value
          }
        })
      }
      this.tellForm(this.value)
    }
  }
}
Vue.component('x-checkbox-group', CheckboxGroup)
