const Form = {
  data() {
    return {
      childrenValue: {},
      reset: 1,
    };
  },
  provide () {
    return {
      formContext: this
    }
  },
  methods: {
    handleBtnClick(type) {
      console.log(type, '监听到click')
      if (type === 'submit') {
        this.$emit('submit', {
          detail: {  
            value: this.childrenValue
          }
        });
      }
      if (type === 'reset') {
        this.$emit('reset')
        this.reset = new Date().getTime()
        // 处理一下childrenValue
        for (let key in this.childrenValue) {
          const val = this.childrenValue[key]
          const type = typeof val
          if (type === 'boolean') {
            this.childrenValue[key] = false
          } else if (type === 'number') {
            this.childrenValue[key] = 0
          } else {
            this.childrenValue[key] = [] // to-do
          }
        }
      }
    }
  },
  created() {
    this.$on('btn-click', this.handleBtnClick)
    this.$on('child-value-change', (a) => {
      console.log(a, '监听到变化')
      this.childrenValue[a.name] = a.value
      console.log(this.childrenValue)
    })
  },
  render(h) {
    const children = this.$slots.default
    return h('wx-form', {
      on: {
        click: this.submit
      }
    }, this.$slots.default)
  }
}
Vue.component('x-form', Form)