const Text = {
  props: {
    selectable: {
      type: Boolean,
      default: false
    },
    space: {
      type: String
    },
    decode: {
      type: Boolean,
      default: false
    }
  },
  render(h) {
    let origin = this.$slots.default[0].text.replace(/\\n/g, '\n')

    let text = origin
      .replace(/&/g, '&amp;')
      .replace(/ /g, '&nbsp;')
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/'/g, '&apos;')
      .replace(/ /g, '&ensp;')
      .replace(/ /g, '&emsp;')
      .replace(/\n/g, '<br />')
    if (!this.decode) {
      text = text
        .replace(/&/g, '&amp;')
    }
    if (this.space) {
      text = text.replace(/ /g, `&${this.space};`)
    }

    return h('wx-text', {
      attrs: {
        selectable: this.selectable
      }
    }, [
      h('span', { style: { display: 'none' } }, origin),
      h('span', {
        domProps: {
          innerHTML: text
        }
      })
    ])
  }
}
Vue.component('x-text', Text)
