const Button = {
  props: {
    size: {
      type: String,
      default: 'default'
    },
    type: {
      type: String
    },
    plain: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    formType: {
      type: String
    },
    hoverClass: {
      type: String,
      default: 'button-hover'
    },
    hoverStartTime: {
      type: Number,
      default: 20
    },
    hoverStopPropagation: {
      type: Boolean,
      default: false
    },
    hoverStayTime: {
      type: Boolean,
      default: 70
    }
  },
  inject: {
    formContext: {default: undefined}
  },
  data() {
    return {
      isActive: false,
      isMoving: false,
      hoverStartTimer: undefined,
      hoverStayTimer: undefined
    }
  },
  methods: {
    touchstart(e) {
      if (!this.disabled) {
        if (this.hoverStopPropagation) {
          e.stopPropagation()
        }
        this.hoverStartTimer = setTimeout(() => {
          this.isActive = true
        }, this.hoverStartTime)
      }
    },
    touchmove(e) {
      if (this.hoverStopPropagation) {
        e.stopPropagation()
      }
      clearTimeout(this.hoverStayTimer)
      clearTimeout(this.hoverStartTimer)
      this.isActive = false
      this.isMoving = true
    },
    touchend(e) {
      if (!this.disabled) {
        if (this.hoverStopPropagation) {
          e.stopPropagation()
        }
        clearTimeout(this.hoverStartTimer)
        this.hoverStayTimer = setTimeout(() => {
          this.isActive = false
        }, this.hoverStayTime)
        if (!this.isMoving) {
          this.$emit('tap')
        }
      }
    },
    handleClick() {
      // emit to-do
      if (this.formType && this.formContext) {
        this.formContext.handleBtnClick(this.formType)
      }
    }
  },
  render(h) {
    const children = this.$slots.default
    return h('wx-button', {
      attrs:
      {
        size: this.size,
        type: this.type,
        disabled: this.disabled,
        plain: this.plain,
        loading: this.loading
      },
      class: [
        `${this.isActive ? this.hoverClass : ''}`
      ],
      on: {
        touchstart: this.touchstart,
        touchmove: this.touchmove,
        touchend: this.touchend,
        click: this.handleClick
      }
    }, children)
  }
}
Vue.component('x-button', Button)
