const Label = {
  props: {
    for: {
      type: String,
    }
  },
  methods: {
    handleLabelClick() {
      const children = this.$slots.default
      // switch,checkbox,radio组件
      const TargetTags = ['x-switch', 'x-checkbox', 'x-radio'];
      let target = null; // 这里只需要找到第一个目标组件,暂时不考虑for属性
      function findTarget(children) {
        children.map(node => {
          if (!target && node.tag && TargetTags.some(o => { return node.tag.indexOf(o) > -1})) {
            target = node
          }
          if (!target && node.children) {
            findTarget(node.children)
          }
        })
      }
      findTarget(children)
      if (target) {
        target.componentInstance.reverse && target.componentInstance.reverse()
      }
    }
  },
  render(h) {
    return h('wx-label', {
      attrs: {
        for: this.for,
      },
      on: {
        click: this.handleLabelClick
      },
    }, this.$slots.default)
  }
}
Vue.component('x-label', Label)