const Navigator = {
  props: {
    target: {
      validator: function (value) {
        return ['self', 'miniProgram'].indexOf(value) !== -1
      },
      default: 'self'
    },
    url: {
      type: String
    },
    openType: {
      validator: function (value) {
        return ['navigate', 'redirect', 'switchTab', 'reLaunch', 'navigateBack', 'exit'].indexOf(value) !== -1
      },
      default: 'navigate'
    },
    delta: {
      type: Number,
      default: 1
    },
    appId: {
      type: String
    },
    path: {
      type: String
    },
    extraData: {
      type: Object
    },
    version: {
      validator: function (value) {
        return ['develop', 'trial', 'release'].indexOf(value) !== -1
      },
      default: 'release'
    },
    hoverClass: {
      type: String,
      default: 'navigator-hover'
    },
    hoverStopPropagation: {
      type: Boolean,
      default: false
    },
    hoverStartTime: {
      type: Number,
      default: 50
    },
    hoverStayTime: {
      type: Number,
      default: 600
    },
    bindsuccess: {
      type: Function
    },
    bindfail: {
      type: Function
    },
    bindcomplete: {
      type: Function
    }
  },
  data() {
    return {
      isHover: false
    }
  },
  render(h) {
    return h('div', {
      class: {
        [this.hoverClass]: this.isHover
      },
      on: {
        click: () => {
          this.onClick()
        },
        touchstart: () => {
          this.isHover = true
        },
        touchend: () => {
          this.isHover = false
        },
        touchmove: () => {
          this.isHover = false
        }
      }
    }, this.$slots.default)
  },
  methods: {
    onClick() {
      switch (this.openType) {
        case 'navigate': {
          if (this.target === 'self') {
            if (this.url) {
              Bridge.navigateTo({
                url: this.url,
                success: this.bindsuccess,
                fail: this.bindfail,
                complete: this.bindcomplete
              })
            } else {
              throw new Error('no url')
            }
          } else if (this.target === 'miniProgram') {
            if (this.appId) {
              Bridge.navigateToMiniProgram({
                appId: this.appId,
                path: this.path,
                extraData: this.extraData,
                envVersion: this.version,
                success: this.bindsuccess,
                fail: this.bindfail,
                complete: this.bindcomplete
              })
            } else {
              throw new Error('no appid')
            }
          }
          break
        }
        case 'redirect': {
          if (this.url) {
            Bridge.redirectTo({
              url: this.url,
              success: this.bindsuccess,
              fail: this.bindfail,
              complete: this.bindcomplete
            })
          } else {
            throw new Error('no url')
          }
          break
        }
        case 'switchTab': {
          if (this.url) {
            Bridge.switchTab({
              url: this.url,
              success: this.bindsuccess,
              fail: this.bindfail,
              complete: this.bindcomplete
            })
          } else {
            throw new Error('no url')
          }
          break
        }
        case 'reLaunch': {
          if (this.url) {
            Bridge.reLaunch({
              url: this.url,
              success: this.bindsuccess,
              fail: this.bindfail,
              complete: this.bindcomplete
            })
          } else {
            throw new Error('no url')
          }
          break
        }
        case 'navigateBack': {
          Bridge.navigateBack({
            delta: this.delta,
            success: this.bindsuccess,
            fail: this.bindfail,
            complete: this.bindcomplete
          })
          break
        }
        case 'exit': {
          if (this.target === 'miniProgram') {
            Bridge.closeProgram()
          } else {
            throw new Error('exit is valid only in miniProgram target')
          }
          break
        }
      }
    }
  }
}
Vue.component('x-navigator', Navigator)
