const Icon = {
  template: '<wx-icon v-bind:size="size" v-bind:type="type"><i v-bind:class="`wx-icon-${type}`" role="img" aria-label v-bind:style="`font-size: ${this.size}px;color: ${color}`"></i></wx-icon>',
  props: {
    type: {
      type: String
    },
    size: {
      type: Number,
      default: 23
    },
    color: {
      type: String
    }
  }
}

Vue.component('x-icon', Icon)
